<template>
    <div>
        <div id="selectBox"> <!--   -->
            <span>Name : </span><input v-model="selectedName" placeholder="Name" class="searchOpt1"> <!-- -->
            <span>Nickname : </span><input v-model="selectedNickname" placeholder="Nickname" class="searchOpt1"> <!-- -->
            <span>Country : </span>
            <select v-model="selectedCountry" class="searchOpt2"> <!-- -->
                <option selected>----</option>
                <option v-for="country in countries" :key ="country.name" value="title" selected>{{ country.name }}</option>
            </select>
            <span>University : </span>
            <select v-model="selectedUniversity" class="searchOpt2"> <!-- -->
                <option selected>----</option>
                <option v-for="university in universities" :key ="university.name" value="title" selected>{{ university.name }}</option>
            </select>
            <span>Major : </span>
            <select v-model="selectedMajor" class="searchOpt2"> <!-- -->
                <option selected>----</option>
                <option v-for="major in majors" :key ="major.name" value="title" selected>{{ major.name }}</option>
            </select>
        </div>
        <br>
        <div id="searchedResult"> <!--   -->
            <table width="100%">
                <th>Name</th>
                <th>Nickname</th>
                <th>Country</th>
                <th>University</th>
                <th>Major</th>
                <tr class ="linedBoard" v-for ="searchedPerson in searchedPeople" :key ="searchedPerson.title">
                    <td align="center" style ="color : #566270;">{{ searchedPerson.name }}</td>
                    <td align="center" style ="color : #566270;">{{ searchedPerson.nickname }}</td>
                    <td align="center" style ="color : #566270;">{{ searchedPerson.country }}</td>
                    <td align="center" style ="color : #566270;">{{ searchedPerson.university }}</td>
                    <td align="center" style ="color : #566270;">{{ searchedPerson.major }}</td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
    export default {
        data () {
            return{
                people: [], //  
                countries : [], //  
                majors : [],
                universities : [],
                selectedName : '',
                selectedNickname: '',
                selectedCountry: '----', //   
                selectedMajor: '----',
                selectedUniversity: '----',
                searchedPeople: [] //   .
            }
        },
        //       .
    }
</script>

<style>
    #selectBox{ /*   . 1*/
        width: 1000px;
        text-align: center;
        margin-left: 250px;
        padding: 10px;
        border: 0.5px solid;
        border-color: #dddfe6;
        background: #dddfe6;
    }
    .searchOpt1{ /*  , .    . 2*/
        width: 100px;
        height: 24px;
        margin: 10px;
    }
    .searchOpt2{ /*  , , . 3*/
        width: 100px;
        height: 30px;
        margin: 10px;
    }
    #searchedResult{ /*   . 1*/
        text-align: center;
        width: 1020px;
        margin-left: 250px;
        border: 1px solid;
        border-color: #dddfe6;
    }
</style>